.App {
    font-family: sans-serif;
    text-align: center;
  }
  
  /* img {
    max-width: 150px;
    height: 15rem;
    padding: 0 15px;
    object-fit: cover;
  } */
  
  img:last-of-type {
    padding-left: 0;
  }

  /* object {
    max-width: 150px;
    height: 15rem;
    padding: 0 15px;
    object-fit: cover;
  } */
  
  object:last-of-type {
    padding-left: 0;
  }
  
  .inner {
    position: relative;
    width: 100%;
    overflow: hidden;
    height: 15rem;
  }
  
  .wrapper {
    /* position: absolute; */
    display: flex;
  }
  
  section {
    display: flex;
    animation: swipe var(--speed) linear infinite backwards;
  }
  
  @keyframes swipe {
    0% {
      transform: translate(0);
    }
  
    100% {
      transform: translate(-100%);
    }
  }
  